"use client";

import ExploreNFTCard from "@/app/components/cards/ExploreNftCard";
import { NFTCard } from "@/app/components/cards/NFTCard";
import NFTModal from "@/app/components/modals/NFTModal";
import { useState } from "react";
import Image from "../../../../../assets/Images/exploreImg.png"
import { useSearchParams } from 'next/navigation';
import { useGetProductById } from "@/api/user/useUser";

const SkeletonExploreNFTCard = ({ isHero = false }: { isHero?: boolean }) => {
  return (
    <div
      className={`overflow-hidden bg-[rgba(32,32,36,0.5)] border border-gray-800 backdrop-blur-sm rounded-lg shadow-sm animate-pulse ${
        isHero ? "lg:flex gap-8 w-full" : ""
      }`}
    >
      <div className={`p-6 ${isHero ? "lg:w-1/2" : ""}`}>
        <div className={`mb-4 ${isHero ? "" : "hidden"}`}>
          <div className="h-6 bg-gray-700 rounded w-1/2 mb-2"></div>
          <div className="h-5 bg-purple-700 rounded w-1/3"></div>
        </div>

        <div className={`${isHero ? "" : "mb-4"}`}>
          {!isHero && (
            <>
              <div className="h-5 bg-gray-700 rounded w-1/2 mb-2"></div>
              <div className="h-5 bg-purple-700 rounded w-1/3"></div>
            </>
          )}
        </div>

        <div className="space-y-2">
          {[...Array(4)].map((_, i) => (
            <div key={i} className="flex justify-between text-gray-400">
              <div className="h-4 w-1/3 bg-gray-700 rounded"></div>
              <div className="h-4 w-1/4 bg-gray-700 rounded"></div>
            </div>
          ))}
        </div>

        <div className="mt-6 h-10 bg-purple-700 rounded w-full" />
      </div>

      <div className={`relative ${isHero ? "lg:w-1/2" : "w-full"}`}>
        <div
          className={`w-full bg-gray-700 ${
            isHero ? "h-[400px] lg:h-full" : "h-48"
          }`}
        ></div>
      </div>
    </div>
  );
};

export default function NFTdetails() {
  const searchParams = useSearchParams();
  const userIdParam = searchParams.get("user_id");
  const userId = userIdParam ? 1 : undefined;
  
  const { data, isLoading } = useGetProductById(userId);

  console.log(data)

  const heroNFT = {
    title: "NFT Name",
    image: "/lovable-uploads/7180623f-7d17-46b5-98bf-e265afdfac15.png",
    price: 10,
    dailyIncome: 2,
    fee: 1,
    days: 10,
    level: "Lv1-Lv3",
    description:
      "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.",
  };

  const popularNFTs = [
    {
      title: "Distant Galaxy",
      image: "https://images.unsplash.com/photo-1526374965328-7f61d4dc18c5",
      price: 1000,
      dailyIncome: 2,
      fee: 1,
      days: 10,
      level: "Lv1-Lv3",
    },
    {
      title: "Distant Galaxy",
      image: "/lovable-uploads/7180623f-7d17-46b5-98bf-e265afdfac15.png",
      price: 1000,
      dailyIncome: 2,
      fee: 1,
      days: 10,
      level: "Lv1-Lv3",
    },
    {
      title: "Distant Galaxy",
      image: "https://images.unsplash.com/photo-1485827404703-89b55fcc595e",
      price: 1000,
      dailyIncome: 2,
      fee: 1,
      days: 10,
      level: "Lv1-Lv3",
    },
    {
      title: "Distant Galaxy",
      image: "https://images.unsplash.com/photo-1526374965328-7f61d4dc18c5",
      price: 1000,
      dailyIncome: 2,
      fee: 1,
      days: 10,
      level: "Lv1-Lv3",
    },
  ];
  const [isModalOpen, setIsModalOpen] = useState(false);
  return (
    <div className="min-h-screen bg-[#0A0A0B] text-white p-4 md:p-8">
      <div className="max-w-7xl mx-auto space-y-12">
        {/* Hero NFT */}
        {isLoading ? (
        <SkeletonExploreNFTCard isHero={true} />
      ) : data ? (
        <ExploreNFTCard
          title={data.title}
          image={data.image}
          price={+data.price}
          dailyIncome={+data.dailyIncome}
          fee={+data.fee}
          days={data.rentalDays}
          level="Lv1-Lv3"
          description={data.description}
          isHero={true}
          onClick={() => setIsModalOpen(true)}
        />
      ) : null}

        {/* Popular NFTs */}
        <div>
          <h2 className="text-2xl font-bold mb-6">Popular NFTS</h2>
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            {popularNFTs.map((nft, index) => (
              <NFTCard
                key={index}
                {...nft}
                action="Buy"
                onClick={() => setIsModalOpen(true)}
              />
            ))}
          </div>
        </div>
      </div>
      <NFTModal
        open={isModalOpen}
        onOpenChange={setIsModalOpen}
        title={data?.title}
        price={data?.price}
        fee={data?.fee}
        image={data?.image}
        type="buy"
      />
    </div>
  );
}
